import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Badge from '@mui/material/Badge';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MailIcon from '@mui/icons-material/Mail';
import NotificationsIcon from '@mui/icons-material/Notifications';
import MoreIcon from '@mui/icons-material/MoreVert';
import { Grid } from "@mui/material";

import NotificationImage from '../../Assets/Notification.png';

import classes from './Header.module.css';


const Header1 = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      id={menuId}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Profile</MenuItem>
      <MenuItem onClick={handleMenuClose}>My account</MenuItem>
    </Menu>
  );

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem>
        <IconButton
          size="large"
          aria-label="show 17 new notifications"
          color="inherit"
        >
          <Badge badgeContent={17} color="error">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <p>Notifications</p>
      </MenuItem>

      <MenuItem>Home</MenuItem>
      <MenuItem>Discover</MenuItem>
      <MenuItem>Activites</MenuItem>
      <MenuItem>About</MenuItem>
      <MenuItem>Contact</MenuItem>

      <MenuItem onClick={handleProfileMenuOpen}>
        <IconButton
          size="large"
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>Profile</p>
      </MenuItem>
    </Menu>
  );



  return (
    <Box sx={{ flexGrow: 1 }}  >
        <AppBar position="static" style={{ background: 'transparent' , boxShadow: 'none'}}>
            <Toolbar>

              <Grid container>

                <Grid item xs={0} sm={0} md={0} lg={0.3} xl={0}></Grid>

                <Grid item xs={6} sm={6} md={3.5} lg={3.5} xl={3.5}>

                  <Grid container>

                    <Grid item xs={1} sm={1} md={1} lg={1} xl={1}>
                      <IconButton
                          size="large"
                          edge="start"
                          color="inherit"
                          aria-label="open drawer"
                          sx={{ mr: 0 , position:'relative' , top:'-2px'}}
                      >
                          {/* <MenuIcon /> */}
                          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                              <path d="M3.414 13.778C2.5 14 2 15.192 2 15.192C2 15.192 5.398 15.626 6.949 17.313C8.5 19 9.071 22.263 9.071 22.263C9.071 22.263 10.47 22.698 10.485 20.849C10.5 19 9.778 17.313 9.778 17.313L13.091 14L16.701 21.704C16.701 21.704 18.08 22.23 18.04 20.365C18 18.5 16.85 10.242 16.85 10.242L19.678 7.413C19.869 7.22851 20.0214 7.00782 20.1262 6.76381C20.231 6.5198 20.2862 6.25736 20.2885 5.99181C20.2908 5.72625 20.2402 5.46289 20.1396 5.21709C20.0391 4.9713 19.8906 4.748 19.7028 4.56021C19.515 4.37243 19.2917 4.22392 19.0459 4.12336C18.8001 4.0228 18.5368 3.97219 18.2712 3.9745C18.0056 3.97681 17.7432 4.03198 17.4992 4.1368C17.2552 4.24162 17.0345 4.39398 16.85 4.585L13.947 7.488C13.947 7.488 6.148 6.09401 3.824 6.297C1.5 6.5 2.559 7.563 2.559 7.563L10.203 11.233L6.95 14.486C6.95 14.486 4.328 13.556 3.414 13.778Z" fill="#2F80ED"/>
                          </svg>
                      </IconButton>
                    </Grid>

                    <Grid item xs={11} sm={11} md={11} lg={11} xl={11}>
                      <Typography  // Use typography to present your design and content as clearly and efficiently as possible.
                            variant="h6"  // like <h6></h6> in HTML
                            noWrap
                            component="div" // like <div></div> in HTML
                            className={classes.brand}
                        >
                            my Dream Place
                        </Typography>
                    </Grid>

                  </Grid>

                </Grid>

                <Grid item xs={5} sm={5} md={6.9} lg={6.9} xl={6.9}>
                  <Grid container className={classes.navbarBtns}>
                      <Grid item xs={12} sm={12} md={1.7} lg={1.5} xl={2} className={classes.gridItem}>
                          <h1>Home</h1>
                      </Grid>
                      <Grid item xs={12} sm={12} md={2.1} lg={1.7} xl={2} className={classes.gridItem}>
                          <h1>Discover</h1>
                      </Grid>
                      <Grid item xs={12} sm={12} md={2.1} lg={1.8} xl={2} className={classes.gridItem}>
                          <h1>Activites</h1>
                      </Grid>
                      <Grid item xs={12} sm={12} md={1.7} lg={1.5} xl={2} className={classes.gridItem}>
                          <h1>About</h1>
                      </Grid>
                      <Grid item xs={12} sm={12} md={1.2} lg={1.8} xl={2} className={classes.gridItem}>
                          <h1>Contact</h1>
                      </Grid>
                  </Grid>
                </Grid>

                {/* <Grid item xs={4} sm={4} md={4} lg={0} xl={0}></Grid> */}

                <Grid item xs={1} sm={1} md={0.9} lg={0.9} xl={0.9} className={classes.notificationBtn}>
                  
                  <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
                    <IconButton
                      size="large"
                      aria-label="show 17 new notifications"
                      color="inherit"
                    >
                      <Badge badgeContent={17} color="error">
                          <NotificationsIcon style={{ color:'rgba(130, 130, 130, 1)'}}/>
                      </Badge>
                    </IconButton>

                    <IconButton
                      size="large"
                      edge="end"
                      aria-label="account of current user"
                      aria-controls={menuId}
                      aria-haspopup="true"
                      onClick={handleProfileMenuOpen}
                      color="inherit"
                    >
                      <img src={NotificationImage} alt='Notification' />
                    </IconButton>

                  </Box>

                  <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
                      <IconButton
                        size="large"
                        aria-label="show more"
                        aria-controls={mobileMenuId}
                        aria-haspopup="true"
                        onClick={handleMobileMenuOpen}
                        color="inherit"
                      >
                        <MoreIcon />
                      </IconButton>
                  </Box>

                </Grid>

                <Grid item xs={0.3} sm={0.3} md={0.3} lg={0.3} xl={0.3}></Grid>
                  
              </Grid>

            </Toolbar>
        </AppBar>
        {renderMobileMenu}
        {renderMenu}
    </Box>
  );
}

export default Header1;